import {
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CBadge,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import EDU_AXIOS from "src/helpers/edu_axios";
import MaterialTable from "material-table";
import { faUser, faEdit, faEye } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useHistory } from "react-router-dom";
function teacher_list(props) {
  let school_id = props.match.params.school_id;
  let history = useHistory();
  let [teachers, setTeachers] = useState([]);

  useEffect(() => {
    let url =
      process.env.REACT_APP_API_PATH +
      "/tm/get-teachers-by-school/" +
      school_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        console.log("response from teachers")
        console.log(resp);
        setTeachers(resp.data.data);
      })

      .catch((error) => {
        console.log(error);
      });
  }, []);
  const columns = [
    {
      title: "Photo",
      field: "logo",
      render: (data) => {
        if (data.logo != "") {
          return (
            <img
              style={{ height: 16, borderRadius: "50%" }}
              src={process.env.REACT_APP_MEDIA_PATH + "/" + data.logo}
            />
          );
        } else {
          return <FontAwesomeIcon icon={faUser} />;
        }
      },
    },
    {
      title: "Name",
      field: "name",
    },
    {
      title: "Email",
      field: "email",
    },
    {
      title: "Mobile",
      field: "mobile",
    },
    {
      title: "classes",
      field: "classes",
      render(data) {
        if(data.classes==undefined)
        {
             return; 
        }
        else{
        return data.classes+",";
      }},
    },
    {
      title: "Primary Subject",
      field: "primary_sub",
    },
    {
      title: "Secondary Subject",
      field: "secondary_sub",
    },
    {
      title: "Status",
      field: "status",
      render: (data) => {
        if (data.status == "active") {
          return <CBadge color="success">Active</CBadge>;
        } else {
          return <CBadge color="success">Active</CBadge>;
        }
      },
    },
  ];
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          {/*Table Start*/}
          <MaterialTable
            title={"All Teachers"}
            data={teachers}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
              pageSize: 15,
              emptyRowsWhenPaging: true,
              pageSizeOptions: [15, 30, 45],
            }}
            actions={[
              {
                icon: () => (
                  <FontAwesomeIcon icon={faEye} style={{ fontSize: "1rem" }} />
                ),
                tooltip: "View Teacher",
                onClick: (event, rowData) =>
                  history.push(
                    `/teacher-management/get-teacher/${school_id}/${rowData.teacher_id}`
                  ),
              },
              {
                icon: () => (
                  <FontAwesomeIcon icon={faEdit} style={{ fontSize: "1rem" }} />
                ),
                tooltip: "Edit Teacher",
                onClick: (event, rowData) =>
                  history.push(
                    `/teacher-management/edit-teacher/${school_id}/${rowData.teacher_id}`
                  ),
              },
            ]}
          />
          {/*Table End* */}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
}

export default teacher_list;
